import React, { useState, useEffect } from "react";
import Speech from "react-speech";
import { useSpeechSynthesis } from "react-speech-kit";
import "./App.css";
import axios from "axios";

import ReactTypingEffect from "react-typing-effect";
import soundfile from "./audio.mp3";
import images from "./helper";

const App = () => {
    const [advice, setAdvice] = useState(
        "Quality is not an act, it is a habit"
    );
    const [doPlay, setDoPlay] = useState(false);
    const [doNarrate, setDoNarrate] = useState(false);
    const [backgroundImg, setBackgroundImg] = useState();
    const { speak } = useSpeechSynthesis();

    const getRndInteger = (min, max) => {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    };
    const setRandomBackGround = () => {
        const id = getRndInteger(0, images.length - 1);
        setBackgroundImg(images[id]);
        console.log("New Backgroud: ", images[id]);
    };
    const playMusic = () => {
        setDoPlay(!doPlay);
    };

    const auto = () => {
        speak({ text: advice });
        console.log("Speaking: ", advice);
    };
    const handleAuto = () => {
        setDoNarrate(!doNarrate);
        console.log("Handleauto: Donarrate: ", doNarrate);
    };
    const handleSpeak = () => {
        speak({ text: advice });
    };

    const fetchAdvice = () => {
        axios
            .get("https://api.adviceslip.com/advice")
            .then((response) => {
                const { advice } = response.data.slip;
                setAdvice(advice);
                console.log(advice.length);
            })
            .catch((error) => {
                console.log(error);
            });
    };
    useEffect(() => {
        console.log("doNarrate ", doNarrate);
        if (doNarrate === true) setTimeout(auto, 200 * advice.length);
    }, [advice]);
    useEffect(() => {
        console.log("component did mount");
        setRandomBackGround();
        let fetchId = setInterval(fetchAdvice, 200 * advice.length);
        console.log("fetchId :", fetchId);
    }, []);

    return (
        <div
            className="app"
            style={{
                background: `linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(${backgroundImg})`,
            }}
        >
            {doPlay && <audio src={soundfile} loop autoPlay />}

            <div className="card">
                <h1 className="header">
                    <ReactTypingEffect speed={70} text={advice} />
                </h1>
                <div className="text-center ">
                    <div className="btn-group">
                        <div style={{ margin: 1 }}>
                            <button
                                className="btn btn-light"
                                onClick={playMusic}
                            >
                                {doPlay ? "Pause Music" : "Play Music"}
                            </button>
                        </div>
                        <div style={{ margin: 1 }}>
                            <button
                                className="btn btn-light"
                                onClick={setRandomBackGround}
                            >
                                Change Image
                            </button>
                        </div>
                        <div style={{ margin: 1 }}>
                            <button
                                className="btn btn-light"
                                onClick={handleSpeak}
                                id="narrate"
                            >
                                Voice Over
                            </button>
                        </div>
                        <div style={{ margin: 1 }}>
                            <button
                                className="btn btn-secondary"
                                onClick={handleAuto}
                                id="narrate"
                            >
                                Auto(beta)
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default App;
